#nav-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 50px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #fff;
  z-index: 100;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

#nav-menu h3 {
  font-size: 24px;
  font-weight: bold;
  margin: 0;
}

#nav-content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-right: 100px;
  align-items: center;
}

#nav-content ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
}

#nav-content li {
  margin-right: 20px;
}

#nav-content a {
  display: block;
  text-decoration: none;
  color: #333;
  font-size: 18px;
  padding: 0px 20px;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}

#nav-content a:hover {
  color: #7d5fff;
}

@media screen and (max-width: 768px) {
  #nav-menu {
    padding: 10px;
  }
  
  #nav-content {
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  }
  
  #nav-content ul {
    display: flex;
    flex-direction: column;
  }
  
  #nav-content li {
    margin-right: 0;
    margin-bottom: 10px;
  }
  
  #nav-content a {
    font-size: 20px;
  }
  
  #nav-toggle:checked ~ #nav-content {
    display: flex;
  }
  
  #nav-toggle-label {
    display: block;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
  
  #nav-toggle-label span {
    display: block;
    width: 25px;
    height: 3px;
    background-color: #333;
    position: absolute;
    top: 24px;
    right: 13px;
    transform: rotate(0deg);
    transition: all 0.3s ease-in-out;
  }
  
  #nav-toggle:checked ~ #nav-toggle-label span {
    background-color: #fff;
  }
  
  #nav-toggle:checked ~ #nav-toggle-label span:nth-of-type(1) {
    transform: rotate(45deg);
    top: 20px;
    right: 10px;
  }
  
  #nav-toggle:checked ~ #nav-toggle-label span:nth-of-type(2) {
    opacity: 0;
  }
  
  #nav-toggle:checked ~ #nav-toggle-label span:nth-of-type(3) {
    transform: rotate(-45deg);
    top: 30px;
    right: 10px;
  }
}

